import angular from 'angular';
import uiRouter from 'angular-ui-router';
import routing from './main.routes';

export class MainController {
  awesomeThings = [];

  /*@ngInject*/
  constructor($http) {
    this.$http = $http;
    this.descriptions = [
      'a fullstack developer focusing on developing mobile apps with hybrid technologies. Passionate about UX, loves playing with new tech and enjoys problem solving. <br> Interested in a full-time remote position but will consider interesting freelance projects.',
    ];
    this.description = '';
  }

  $onInit() {
    this.description = this.descriptions[
      Math.floor(Math.random() * this.descriptions.length)
    ];
  }
}

export default angular
  .module('pocorschiComApp.main', [uiRouter])
  .config(routing)
  .component('main', {
    template: require('./main.html'),
    controller: MainController,
    controllerAs: 'main',
  }).name;



